// use em to control relative position between contract and signature position
* {
    box-sizing: border-box;
}

body {
    overflow: hidden; // prevent broswer reload or pre/next page
}

$base_fontSize: 16px;

.container {
    @media screen and (orientation: landscape) {
        div {
            display: none;
        }
        width: 100vw;
        background-color:#02a7bc;
        display: flex;
        justify-content: center;
        align-items: center;
        &:after {
            display: block;
            content: '請改回直式瀏覽 ᕕ ( ᐛ ) ᕗ';
            color: white;
            font-size: 2rem;
        }
    }
    position: absolute;
    top: 0;
    left: 0;
    font-size: $base_fontSize;
    width: 65em;
    min-height: 100%;
    overflow: hidden;
    perspective: 600px;
    &:global.active {
        &:before {
            @media screen and (orientation: landscape) {
                display: none;
            }
            display: block;
            content: '送出成功！';
            padding: 5vw 10vw;
            font-size: 10vw;
            border-top: 1vw solid gray;
            border-bottom: 1vw solid gray;
            position: absolute;
            white-space: nowrap;
            top: 0;
            right: 50%;
            z-index: -1;
            transform: rotateZ(-90deg) rotateY(-10deg);
            transform-origin: right center;
        }
    }
}

$signature_width: 14.3 * $base_fontSize;
$signature_height: 4.6 * $base_fontSize;
$signature_top: 61.5 * $base_fontSize;
$signature_left: 49.2 * $base_fontSize;

.box__contract {
    transition: transform 1s;
    transform-origin: #{$signature_left+$signature_width/2} #{$signature_top+$signature_height/2};
}

.box__contract--hint {
    transform: translate(-37%, 15vh) scale(2.6);
}

.img__contract {
    width: 65em;
    cursor: pointer;
}

.signature_position {
    position: absolute;
    width: $signature_width;
    height: $signature_height;
    display: flex;
    justify-content: center;
    align-items: center; // background: #0e536f;
    z-index: 1;
    border: 0.3rem dashed red; // right: 2.2em;
    left: $signature_left;
    animation: signatureArea 1s linear infinite alternate;
    text-align: center;
    line-height: 2.7em;
    color: green;
    font-weight: bold;
    cursor: pointer;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    &:after {
        content: 'Click to signature!';
        font-size: 1.2em;
    }
    &:before {
        content: '';
    }
    &:global.active {
        animation: none;
        &:after {
            display: none;
        }
    }
}

.signature_requirement {
    top: $signature_top;
}

.signature_requirement2 {
    top: 66em;
}

@keyframes signatureArea {
    from {
        opacity: 0.4;
    }
    to {
        opacity: 1;
    }
}

$btn_search_size:20vw;
.btn__search {
    position: fixed;
    right: 5vw;
    bottom: 5vw;
    width: $btn_search_size;
    height: $btn_search_size;
    background: blue;
    border-radius: 50%;
    z-index: 3;
}

.btn__confirm {
    background: salmon;
    position: absolute;
    bottom: 5vw;
    left: 0;
    right: 0;
    width: 35vw;
    font-size: 6vw;
    text-align: center;
    padding: 1vw;
    line-height: 10vw;
    border-radius: 1vw;
    margin: auto;
    transform: translateY(200%);
    transition: transform 0.5s linear;
    pointer-events: none;
    &:global.active {
        transform: translateY(0);
        pointer-events: visible;
    }
}



