$title_text_shadow_color: black;

.container {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0; // background: rgba(0, 0, 0, 0.5);
    z-index: 300;
    h1 {
        position: absolute;
        top: calc( 16px * 45 + 15vh);
        left: 50%;
        transform: translateX(-50%);
        font-size: 5vh;
        white-space: nowrap;
        color: white;
        text-shadow: 2px 2px 1vh rgba($title_text_shadow_color, 1), -1vh -2px 1vh rgba($title_text_shadow_color, 1), 2px -2px 1vh rgba($title_text_shadow_color, 1), -2px 2px 1vh rgba($title_text_shadow_color, 1);
        opacity: 0;
    }
}

.svg__bg {
    fill: rgba(0, 0, 0, 0.6);
}

.svg__logo {
    transform-origin: center;
}

.svg__hintCircle {
    transform-origin: center; // animation: lightAnimation 3s linear forwards;
}

